.resetPasswordContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(231, 231, 231);
    position: fixed;
    top: 0%;
    left: 0;
}

.resetPasswordBox {
    background-color: white;
    width: 25vw;
    height: 70vh;
    box-sizing: border-box;
    overflow: hidden;
}

.resetPasswordHeading {
    text-align: center;
    color: rgba(0, 0, 0, 0.664);
    font: 400 1.3vmax "Roboto";
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    width: 50%;
    margin: auto;
}

.resetPasswordBox>div>button {
    background-color: rgb(94, 94, 220);
    height: 3px;
    width: 50%;
    border: none;
    transition: all 0.5s;
}

.resetPasswordForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 70%;
    transition: all 0.5s;
}




.resetPasswordForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}


.resetPasswordForm>div>input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;
}


.resetPasswordForm>div>svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}


#resetPasswordImage>img {
    width: 3vmax;
    border-radius: 100%;
    margin:1vmax;
}

#resetPasswordImage>input {
    display: flex;
    padding: 0%;
}


#resetPasswordImage>input::file-selector-button {
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0%;
    font: 400 0.8vmax cursive;
    transition: all 0.5s;
    padding: 0 1vmax;
    color: rgba(0, 0, 0, 0.623);
    background-color: rgb(255, 255, 255);
}

#resetPasswordImage>input::file-selector-button:hover {
    background-color: rgb(235, 235, 235);
}


.resetPasswordBtn {
    border: none;
    background-color: rgb(94, 94, 220);
    color: white;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.resetPasswordBtn:hover {
    background-color: rgb(179, 66, 46);
}


@media screen and (max-width: 600px) {
    .hide {
        display: none;
    }

    .resetPasswordContainer {
        background-color: white;
    }

    .resetPasswordBox {
        width: 100vw;
        height: 95vh;
    }



    .resetPasswordForm {
        padding: 5vmax;
    }

    .resetPasswordForm>div>input {
        padding: 2.5vmax 5vmax;
        font: 300 1.7vmax cursive;
    }

    .resetPasswordForm>div>svg {
        font-size: 2.8vmax;
    }


    #resetPasswordImage>img {
        width: 8vmax;
        border-radius: 100%;
    }

    #resetPasswordImage>input::file-selector-button {
        height: 7vh;
        font: 400 1.8vmax cursive;
    }

    .resetPasswordBtn {
        font: 300 1.9vmax "Roboto";
        padding: 1.8vmax;
    }
}