.input{
    width: 300px;
    padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
.button{
    border: none;
    background-color: rgb(94, 94, 220);
    color: white;
    font: 300 1vmax "Roboto";
    padding: 1vmax;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
    margin: 2vmax;
    border-radius: 1vmax;
}
.button:hover {
    background-color: rgb(179, 66, 46);
}

.form{
    margin-top: 44px;
    margin-left: 20px;
}









